@-webkit-keyframes skeletonAnimation {
  0% {
    background-position: -150px; }
  40% {
    background-position: 350px; }
  100% {
    background-position: 350px; } }

@keyframes skeletonAnimation {
  0% {
    background-position: -150px; }
  40% {
    background-position: 350px; }
  100% {
    background-position: 350px; } }

.skeleton {
  -webkit-animation: skeletonAnimation 2s infinite linear;
          animation: skeletonAnimation 2s infinite linear;
  background-color: #CCC;
  background-image: -webkit-linear-gradient(left, #DDD 0, #E8E8E8 50px, #DDD 100px);
  background-image: -o-linear-gradient(left, #DDD 0, #E8E8E8 50px, #DDD 100px);
  background-image: linear-gradient(90deg, #DDD 0, #E8E8E8 50px, #DDD 100px);
  background-size: 600px;
  border-radius: 2px;
  margin-bottom: 10px;
  max-height: 8px;
  max-width: 300px; }

.skeletonHiddenChild {
  color: transparent;
  margin-bottom: -1px;
  visibility: hidden; }



